
import { mapState, mapActions } from "vuex";
import { defineComponent } from "vue";
import { required } from "@vuelidate/validators";
import Status from "@/enums/StatusEnum";
import useVuelidate from "@vuelidate/core";

export default defineComponent({
  name: "TaskDetailsModal",
  mounted() {
    console.log(this.v$);
  },
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      title: "",
      description: "",
      time: "",
      WantEdit: false,
      NotEdit: true,
      WantSave: true,
      Status,
    };
  },
  props: ["currentTask"],
  validations() {
    return {
      title: { required },
      description: { required },
      time: { required },
    };
  },
  computed: {
    ...mapState([]),
  },
  methods: {
    ...mapActions(["CREATE_NEW_TASK", "CHANGE_TASK"]),
    Edit() {
      this.title = this.currentTask.title;
      this.description = this.currentTask.text;
      this.time = this.currentTask.time;
      this.NotEdit = false;
      this.WantEdit = true;
    },
    removeEditTask() {
      this.$emit("removeEditTask");
      this.NotEdit = true;
      this.WantEdit = false;
      this.v$.title.$dirty = false;
      this.v$.description.$dirty = false;
      this.v$.time.$dirty = false;
    },
    changeTask() {
      this.v$.$validate();
      if (!this.v$.$error) {
        const changedTask = {
          title: this.title,
          text: this.description,
          time: this.time,
          id: this.currentTask.id,
        };
        this.CHANGE_TASK(changedTask);
        this.removeEditTask();
      } else {
        this.v$.$dirty = false;
        console.log(this.v$);
      }
    },
  },
});
